import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
import DashboardTabs from '@/components/DashboardTabs.vue';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      tableData: [],
      dealedData: [],
      // Ê†πÊçÆIPÂú∞ÂùÄÂ§ÑÁêÜÂêéÁöÑÊï∞ÊçÆÔºåÊ†ºÂºè‰∏∫ {ip, count, [data]}
      blockipList: [],
      // Á¶ÅÊ≠¢‰∏ä‰º†ÁöÑIPÂàóË°®

      loading: false,
      // ÂàÜÈ°µÊï∞ÊçÆ
      currentPage: 1,
      pageSize: 10 // ÈªòËÆ§ÊØèÈ°µ10Êù°
    };
  },
  components: {
    DashboardTabs
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    },
    paginatedData() {
      // ËÆ°ÁÆóÂàÜÈ°µÊï∞ÊçÆ
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.dealedData.slice(start, end);
    }
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // ÂºÄÂèëÁéØÂ¢É, url ÂâçÈù¢Âä†‰∏ä /api
      // url = `/api${url}`;
      if (this.credentials) {
        // ËÆæÁΩÆ Authorization Â§¥
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // Á°Æ‰øùÂåÖÂê´Âá≠ÊçÆÔºåÂ¶Ç cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('ËÆ§ËØÅÁä∂ÊÄÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    formatTimeStamp(timeStamp) {
      return new Date(timeStamp).toLocaleString();
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    },
    async handleSwitchEnable(row) {
      const ip = row.ip;
      const enable = row.enable;
      if (enable) {
        // ‰ªé blockipList ‰∏≠ÁßªÈô§
        this.blockipList = this.blockipList.filter(item => item !== ip);
        // Êõ¥Êñ∞ blockipList
        await this.fetchWithAuth("/api/manage/cusConfig/whiteip", {
          method: 'POST',
          body: ip
        });
      } else {
        // Ê∑ªÂä†Âà∞ blockipList ‰∏≠
        this.blockipList.push(ip);
        // Êõ¥Êñ∞ blockipList
        await this.fetchWithAuth("/api/manage/cusConfig/blockip", {
          method: 'POST',
          body: ip
        });
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
      // Âà∞ÊúÄÂêé‰∏ÄÈ°µÊó∂ÔºåÂä†ËΩΩÊõ¥Â§öÊï∞ÊçÆ
      if (page === Math.ceil(this.dealedData.length / this.pageSize)) {
        this.loadMoreData();
      }
    },
    loadMoreData() {
      this.loading = true;
      const start = this.dealedData.length;
      const count = 20; // ÊØèÊ¨°Âä†ËΩΩ20Êù°Êï∞ÊçÆ
      this.fetchWithAuth(`/api/manage/cusConfig/list?start=${start}&count=${count}`, {
        method: 'GET'
      }).then(response => response.json()).then(result => {
        this.dealedData = this.dealedData.concat(result.map(item => {
          const enable = !this.blockipList.includes(item.ip);
          return {
            ip: item.ip,
            count: item.count,
            data: item.data,
            enable: enable
          };
        }));
      }).catch(() => {
        this.$message.error('Âä†ËΩΩÊõ¥Â§öÊï∞ÊçÆÊó∂Âá∫ÈîôÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•');
      }).finally(() => {
        this.loading = false;
      });
    },
    handleSizeChange(size) {
      this.pageSize = size;
      this.currentPage = 1;
    }
  },
  mounted() {
    this.loading = true;
    this.fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // Âú® check ÊàêÂäüÂêéÂÜçÊâßË°å list ÁöÑ fetch ËØ∑Ê±Ç
        return this.fetchWithAuth("/api/manage/cusConfig/list?count=20", {
          method: 'GET'
        });
      } else if (result == "Not using basic auth.") {
        return this.fetchWithAuth("/api/manage/cusConfig/list?count=20", {
          method: 'GET'
        });
      } else {
        throw new Error('Unauthorized');
      }
    }).then(response => response.json()).then(async result => {
      // ËØªÂèñblockipList, Êé•Âè£ËøîÂõûÊ†ºÂºè‰∏∫ 'ip1,ip2,ip3'ÔºåÈúÄË¶ÅËΩ¨Êç¢‰∏∫Êï∞ÁªÑ
      const blockipList = await this.fetchWithAuth("/api/manage/cusConfig/blockipList", {
        method: 'GET'
      });
      this.blockipList = (await blockipList.text()).split(',');
      this.dealedData = result.map(item => {
        const enable = !this.blockipList.includes(item.ip);
        return {
          ip: item.ip,
          count: item.count,
          data: item.data,
          enable: enable
        };
      });
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('ÂêåÊ≠•Êï∞ÊçÆÊó∂Âá∫ÈîôÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•');
      }
    }).finally(() => {
      this.loading = false;
    });
  }
};